#Home {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

#Home ul {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0;
}

#Home ul li {
    font-weight: bold;
    letter-spacing: 1px;
    margin: 10px 0;
}